.infoBox tr {
  font-family: 'Roboto', sans-serif;
  font-weight: 300; }

html, body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400; }

.infoBox h3 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500; }

header {
  position: absolute;
  top: 0;
  left: 0;
  height: 50px;
  width: 100%;
  z-index: 500;
  background-color: #3F51B5; }
  header h2 {
    float: left;
    color: white;
    padding: 0 20px;
    margin: 0;
    line-height: 50px; }
  header .search--box {
    float: right;
    margin: 10px 20px 0 0; }
    @media screen and (max-width: 520px) {
      header .search--box {
        display: none; } }
    header .search--box input[type=text] {
      border: 0;
      border-bottom: 1px solid white;
      border-radius: 0;
      background-color: #3F51B5;
      outline: none;
      color: white; }
    header .search--box input[type=submit] {
      border: 0;
      background: #00897b;
      outline: none;
      color: white;
      padding: 5px 10px;
      border-radius: 2px;
      margin-left: 5px;
      transition: background 200ms; }
      header .search--box input[type=submit]:hover {
        background: #26a69a; }

.infoBox {
  position: fixed;
  width: 50%;
  right: -100%;
  top: 50px;
  bottom: auto;
  height: 100%;
  background: rgba(255, 255, 255, 0.93);
  overflow: scroll;
  border-left: 2px solid #CECECE;
  transition: right 400ms, bottom 400ms; }
  .infoBox.visible {
    right: 0; }
  @media screen and (max-width: 768px) {
    .infoBox {
      width: 100%;
      height: 50%;
      bottom: -50%;
      top: auto;
      left: 0;
      border-top: 2px solid #CECECE; }
      .infoBox.visible {
        bottom: 0; } }
  .infoBox .close {
    position: absolute;
    right: 5%;
    top: 10px;
    padding: 10px;
    cursor: pointer;
    font-size: 1.3em;
    transition: color 200ms; }
    .infoBox .close:hover {
      color: #00897b; }
  .infoBox h3 {
    padding: 0 5%; }
  .infoBox table {
    width: 90%;
    margin: 10px 5% 80px 5%;
    text-align: left;
    padding-bottom: 20px; }
  .infoBox tr {
    border-bottom: 1px solid #CECECE; }
  .infoBox td {
    padding: 5px 0; }
  .infoBox .messages {
    width: 90%;
    margin: 0 5% 30px 5%; }
    .infoBox .messages span {
      display: block;
      width: 90%;
      padding: 2% 5%;
      margin-bottom: 5px; }
    .infoBox .messages .infoMessages {
      background: #d9edf7; }
    .infoBox .messages .importantMessages {
      background: #fcf8e3; }
    .infoBox .messages .criticalMessages {
      background: #f2dede; }

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #333333; }

#map {
  width: 100%;
  height: 100%; }
